@import "./normalize.css";

/*#region Global*/
*:focus {
  box-shadow: 0 0 0 4px rgba(0, 155, 255, 0.5);
  outline: none;
}

::selection {
  background: rgba(0, 0, 0, 0.5);
}

body {
  position: relative;
  min-height: 100vh;
  margin: 0;
  font-family: 'Titillium Web', system, -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-size: 16px;
  line-height: 1.5;
  color: #474747;
  background: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: 'Titillium Web', system, -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-weight: 400;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  margin-bottom: 8px;
  font-size: 32px;
  line-height: 1.2;
  color: #474747;
}

.title--head {
  text-align: center;
}

.title--medium {
  margin-bottom: 8px;
  font-size: 28px;
}

.title--small {
  margin-bottom: 8px;
  font-size: 24px;
}

.title--invert {
  color: #fff;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .container {
    padding: 0;
  }

  .title {
    margin-bottom: 16px;
    font-size: 52px;
  }

  .title--medium {
    margin-bottom: 12px;
    font-size: 40px;
  }
  
  .title--small {
    margin-bottom: 8px;
    font-size: 30px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Hero*/
.hero {
  height: 90vh;
  color: #fff;
  background: url('../img/bg-top.svg') top center no-repeat;
  background-size: cover;
}

.hero > .container {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 4vh 20px 2vh;
}

.hero__logotype {
  margin-bottom: 20px;
}

.hero__subtitle {
  margin-bottom: 20px;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .hero > .container {
    padding: 16vh 0 12vh;
  }
  
  .hero__logotype {
    margin-bottom: 60px;
  }
  
  .hero__subtitle {
    margin-bottom: 80px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Features*/
.features > .container {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  margin: 40px auto;
}

.feature {
  display: flex;
  flex-flow: row;
  align-items: center;
  margin: 20px 0;
  text-align: left;
}

.feature__icon {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  width: 100px;
  height: 100px;
  margin: 24px;
  border-radius: 50%;
  box-shadow: 0 12px 62px 0 rgba(0, 0, 0, 0.125);
  background: #fff;
  align-content: center;
  transition: 0.3s;
}

.feature:hover > .feature__icon {
  box-shadow: 0 20px 82px 0 rgba(0, 0, 0, 0.3);
  transform: translateY(-8px);
}

.feature__icon > img {
  width: 32px;
}

.feature__title {
  padding: 0 8px;
  font-size: 32px;
  font-weight: 500;
  color: #474747;
}

.feature__descr {
  padding: 0 8px;
  font-size: 24px;
  line-height: 1.2;
  color: #6f6f6f;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .features > .container {
    flex-flow: row;
    margin: 120px auto;
  }

  .feature {
    flex-flow: column;
    align-items: center;
    flex-grow: 1;
    flex-basis: 0;
    margin: 0 auto;
    text-align: center;
  }

  .feature__title {
    padding: 0 20px;
    font-size: 40px;
  }
  
  .feature__descr {
    padding: 0 20px;
    font-size: 30px;
  }

  .feature__icon {
    width: 140px;
    height: 140px;
    margin: 32px;
  }

  .feature__icon > img {
    width: 48px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Preview*/
.preview {
  display: flex;
  align-items: center;
  position: relative;
  max-width: 100%;
  flex-flow: column;
  padding: 0;
}

.preview > * {
  position: relative;
  z-index: 1;
}

.preview > .title {
  padding: 0 20px;
  color: #474747;
}

.preview__title {
  margin-bottom: 32px;
}

.preview__bg {
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  z-index: 0;
  width: 100%;
  max-width: 100%;
  margin: 10% auto 0;
  object-fit: cover;
}

.preview__bg > svg {
  display: block;
  width: 200%;
  height: 100%;
}

.preview__img {
  position: relative;
  max-width: 1208px;
}

#buble__bg {
  animation: breath 5s cubic-bezier(0.445, 0.05, 0.55, 0.95) 0s infinite;
}

.buble__figure {
  animation: buble 10s cubic-bezier(0.645, 0.045, 0.355, 1) 0s infinite;
}

.buble__figure2 {
  animation: buble2 10s cubic-bezier(0.645, 0.045, 0.355, 1) 0s infinite;
}

@keyframes breath {
  0% {
    opacity: 1;
    transform: scale(1) rotateX(-20deg) translate(0, 0);
  }
  50% {
    opacity: 0.8;
    transform: scale(0.95) rotateX(15deg) translate(10px, 30px);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotateX(-20deg) translate(0, 0);
  }
}

@keyframes buble {
  0% {
    opacity: 1;
    transform: scale(1) rotateX(0) translate(0, 0);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.2) rotateX(15deg) translate(40px, 50px);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotateX(0) translate(0, 0);
  }
}

@keyframes buble2 {
  0% {
    opacity: 1;
    transform: scale(1) rotateX(0) translate(0, 0);
  }
  50% {
    opacity: 0.8;
    transform: scale(0.8) rotateX(20deg) translate(50px, 20px);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotateX(0) translate(0, 0);
  }
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .preview {
    padding: 200px 0 60px;
  }

  .preview__bg {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    z-index: 0;
    max-width: none;
    margin: 0 auto 0;
  }
}

@media only screen and (min-width: 1200px) {
  .preview > .title {
    color: #fff;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Details*/
.details {
  margin-top: 40px;
}

.detail {
  display: flex;
  flex-flow: column;
  width: fit-content;
  margin: 20px auto;
}

.detail[data-emergence=hidden] {
  opacity: 0;
  transform: translateY(-50px);
  transition: all 600ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.detail[data-emergence=visible] {
  opacity: 1;
  transform: translateY(0);
  transition: all 600ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.detail--flipped {
  flex-flow: column;
}

.detail__preview {
  margin-top: 32px;
}

.detail__preview > img {
  width: 100%;
  max-width: 400px;
}

.detail__descr {
  max-width: 480px;
  font-size: 22px;
  color: #6f6f6f;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .details {
    margin-top: 0;
  }

  .detail {
    flex-flow: row;
    align-items: center;
    justify-content: space-between;
    margin: 60px 0;
  }

  .detail--flipped {
    flex-flow: row-reverse;
  }

  .detail > .detail__info {
    margin-right: 120px;
  }

  .detail--flipped > .detail__info {
    margin-right: 0;
    margin-left: 120px;
  }

  .detail__preview {
    margin-top: 0;
  }

  .detail__preview > img {
    max-width: 630px;
  }

  .detail__descr {
    font-size: 30px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Action*/
.action {
  margin-top: -72px;
  color: #fff;
  background: url('../img/bg-down.svg') bottom center no-repeat;
}

.action > .container {
  display: flex;
  flex-flow: column;
  align-items: center;
  min-height: 520px;
}

.action__title {
  margin-top: 200px;
}

.action__descr {
  margin-bottom: 48px;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1200px) {
  .action {
    margin-top: -160px;
  }
  
  .action > .container {
    min-height: 660px;
  }

  .action__title {
    margin-top: 320px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Footer*/
.footer {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  padding: 16px 20px;
}

.footer__menu {
  margin-top: 16px;
}

.made-by {
  display: flex;
  font-size: 22px;
}

.made-by a {
  text-decoration: none;
  color: currentColor;
}

.made-by a:hover {
  text-decoration: underline;
}

.made-by > .icon-heart {
  margin: 0 12px;
}

.copyright {
  font-size: 18px;
  color: #6f6f6f;
}

.link {
  margin: 0 16px;
  font-size: 24px;
  text-decoration: none;
  color: #474747;
  transition: 0.125s;
}

.link:first-of-type {
  margin-left: 0;
}

.link:last-of-type {
  margin-right: 0;
}

.link:hover {
  color: #ed5656;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1200px) {
  .footer {
    flex-flow: row;
    justify-content: space-between;
    padding: 24px 64px;
  }

  .footer__menu {
    margin-top: 0;
  }

  .made-by {
    font-size: 24px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Buttons*/
.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 200px;
  padding: 12px 16px;
  border: none;
  border-radius: 6px;
  box-shadow: 0 32px 52px 0 rgba(0, 0, 0, 0.2);
  font-size: 24px;
  text-decoration: none;
  font-weight: 500;
  color: #ee6f6f;
  background: #fff;
  transition: 0.3s;
}

.button:focus,
.button:hover {
  box-shadow: 0 16px 40px 0 rgba(0, 0, 0, 0.2);
  background: #474747;
  transform: translateY(2px);
  cursor: pointer;
}

.button__icon::after {
  width: 2px;
  height: 100%;
  margin: 0 16px;
  background: rgb(253, 234, 234);
  transition: 0.3s;
  content: '';
}

.button:focus > .button__icon::after,
.button:hover > .button__icon::after {
  background: rgb(95, 78, 78);
}

.button--invert {
  color: #fff;
  background: #ed5656;
}

.button__icon {
  display: flex;
  width: fit-content;
  height: 24px;
}

.button__icon > img {
  width: 24px;
  height: 24px;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .button {
    min-width: 240px;
    padding: 16px 24px;
    font-size: 32px;
  }

  .button__icon {
    height: 38px;
  }
  
  .button__icon > img {
    width: 38px;
    height: 38px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Form*/
.form__brand {
  width: 100px;
  height: 100px;
  margin: 0 auto 40px;
}

.input {
  margin: 8px 0;
}

.input__label {
  display: flex;
  align-items: center;
  font-size: 24px;
}

.input__field {
  box-sizing: border-box;
  width: 90vw;
  max-width: 512px;
  padding: 8px 16px;
  border: none;
  border-radius: 3px;
  font-size: 18px;
  color: #474747;
  background: #f3f3f3;
}

.input__field--textarea {
  min-height: 120px;
}

.input__label-icon {
  display: inline-flex;
  align-items: center;
  width: 24px;
  height: 24px;
  padding-right: 16px;
}

.input__label-icon::after {
  flex-shrink: 0;
  width: 2px;
  height: 90%;
  margin: 0 0 0 8px;
  background: #e3e3e3;
  content: '';
}

.form_hidden {
  display: none;
}

/*#region Media Desctop*/
@media only screen and (min-width: 1000px) {
  .input {
    margin: 8px 0;
  }
  
  .input__label {
    font-size: 32px;
  }
  
  .input__field {
    padding: 16px 24px;
    font-size: 24px;
  }

  .input__field--textarea {
    min-height: 200px;
  }
  
  .input__label-icon {
    width: 68px;
    height: 68px;
    padding-right: 0;
  }
  
  .input__label-icon::after {
    height: 54%;
    margin: 0 16px;
  }
}

/*#endregion*/

/*#endregion*/

/*#region Utility*/
.center-all {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}

/*#endregion*/
